@import '../../../public/css/colors';

.category{
    margin-top: -40px;
    background: $head;
    border-radius: 10px;
    transition: .5s;
    margin-bottom: 2rem;
}
.category:hover{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.icon{
    color: #000;
    background: $head;
    font-size: 20px;
    width: 100%;
}